import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EvaluateDetailResolver } from '@app/modules/question-evaluate/evaluate-reslover/evaluate-detail.reslover';
import { SingleListeningEvaluateComponent } from '@app/modules/question-evaluate/single-listening-evaluate/single-listening-evaluate.component';
import { SingleReadingEvaluateComponent } from '@app/modules/question-evaluate/single-reading-evaluate/single-reading-evaluate.component';
import { SingleSpeakingEvaluateComponent } from '@app/modules/question-evaluate/single-speaking-evaluate/single-speaking-evaluate.component';

import { WritingEvaluateComponent } from '@app/modules/question-evaluate/writing-evaluate/writing-evaluate.component';


const routes: Routes = [
  {
    path: ':moduleName/:questionType/:questionId/writing-evaluate',
    component: WritingEvaluateComponent,
    resolve:{
      questionDetail:EvaluateDetailResolver
    }
  },
  {
    path: ':moduleName/:questionType/:questionId/reading-evaluate',
    component: SingleReadingEvaluateComponent,
    resolve:{
      questionDetail:EvaluateDetailResolver
    }
  },
  {
    path: ':moduleName/:questionType/:questionId/speaking-evaluate',
    component: SingleSpeakingEvaluateComponent,
    resolve:{
      questionDetail:EvaluateDetailResolver
    }
  },
  {
    path: ':moduleName/:questionType/:questionId/listening-evaluate',
    component: SingleListeningEvaluateComponent,
    resolve:{
      questionDetail:EvaluateDetailResolver
    }
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers:[
    EvaluateDetailResolver
  ]
})
export class QuestionEvaluateRoutingModule { }