import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EvaluateQuestionService } from '@app/core/http/evaluate-question.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-single-listening-evaluate',
  templateUrl: './single-listening-evaluate.component.html',
  styleUrls: ['./single-listening-evaluate.component.css']
})
export class SingleListeningEvaluateComponent implements OnInit {
  public routeParam;
  public detailData:any;
  public subscription:Subscription;
  public resultScore:any;
  
  
  constructor(private _activeRoute:ActivatedRoute,private _service:EvaluateQuestionService) { }

  ngOnInit() {
    this.routeParam=this._activeRoute.snapshot.params;
    this.detailData = this._activeRoute.snapshot.data["questionDetail"];
  } 


  getAnswer(answer){
    this.subscription = this._service.onManageListeningEvaluate(answer, this.routeParam)
    .subscribe((res) => {
      this.resultScore = res;
      this.resultScore.questionType=this.routeParam.questionType
    })

  }

}
