import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EvaluateQuestionService } from '@app/core/http/evaluate-question.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-single-reading-evaluate',
  templateUrl: './single-reading-evaluate.component.html',
  styleUrls: ['./single-reading-evaluate.component.css']
})
export class SingleReadingEvaluateComponent implements OnInit {

  public routeParam;
  public detailData:any;
  
  private subscription:Subscription;
  public resultScore:any;

  constructor(private _activeRoute:ActivatedRoute, private readingService: EvaluateQuestionService) { }
  
  ngOnInit() {
    this.routeParam=this._activeRoute.snapshot.params;
    this.detailData = this._activeRoute.snapshot.data["questionDetail"]
  }


  getAnswer(answer){
    this.subscription = this.readingService.onManageReadingEvaluate(answer, this.routeParam)
    .subscribe((res) => {
      this.resultScore = res;
      this.resultScore.questionType=this.routeParam.questionType
    })
  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    if(this.subscription) this.subscription.unsubscribe();
  }

}
