import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EvaluateQuestionService } from '@app/core/http/evaluate-question.service';
import { EssayQuestionComponent } from '@app/shared/question-components/essay-question/essay-question.component';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-writing-evaluate',
  templateUrl: './writing-evaluate.component.html',
  styleUrls: ['./writing-evaluate.component.css']
})
export class WritingEvaluateComponent implements OnInit, OnDestroy {
  public routeParam
  public detailData: any;
  public resultScore: any;

  private _subscription: Subscription;


  constructor(private _activeRoute: ActivatedRoute, private writingService: EvaluateQuestionService) { }

 

  ngOnInit() {
    this.routeParam = this._activeRoute.snapshot.params;
    this.detailData = this._activeRoute.snapshot.data["questionDetail"]
  }

  onSubmit(data) {
    this._subscription = this.writingService.onManageWritingEvaluate(data, this.routeParam)
      .subscribe((res) => {
        this.resultScore = res;
      })
  }

  ngOnDestroy(): void {
    if (this._subscription)
      this._subscription.unsubscribe();
  }

}
